.zone-filter-field {
  .input-wrapper {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 100%;
  }

  .ac-row {
    display: flex;
    align-items: stretch;
    width: 100%;
    gap: 0.25rem;
  }

  .ac-row .p-autocomplete,
  .ac-row .p-autocomplete .p-inputtext {
    width: 100%;
  }

  .btn-icon.toggle,
  .btn-icon.clear {
    flex: 0 0 auto;
    height: 2.5rem;
    width: 2.5rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.375rem;
    cursor: pointer;
    transition:
      background 0.15s ease,
      color 0.15s ease;
  }

  .tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.4rem;
  }

  .tag.pill {
    display: inline-flex;
    align-items: center;
    max-width: 100%;
    font-size: 0.75rem;
    line-height: 1;
    padding: 0.4rem 0.55rem 0.4rem 0.6rem;
    border-radius: 9999px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06);
    position: relative;
    transition: background 0.15s ease;
  }

  .tag-label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 12rem;
  }

  .tag-remove {
    background: transparent;
    color: #ffffff;
    border: none;
    margin-left: 0.4rem;
    cursor: pointer;
    font-size: 0.85rem;
    line-height: 1;
    padding: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  .tag-remove:hover {
    color: #fef3c7;
  }

  .popup.af-popup {
    min-width: 420px;
    max-width: 560px;
  }

  .custom-tabview {
    ::ng-deep {
      .p-tabview-nav {
        background: transparent;
        border-bottom: 1px solid #e5e7eb;

        .p-tabview-nav-link {
          background: transparent;
          border: none;
          border-bottom: 2px solid transparent;
          color: #6b7280;
          font-weight: 500;
          padding: 0.75rem 1rem;
          transition: all 0.2s ease;

          &:not(.p-disabled):focus {
            box-shadow: none;
            outline: 2px solid #3b82f6;
            outline-offset: -2px;
          }

          &:hover {
            background: rgba(59, 130, 246, 0.05);
            color: #3b82f6;
          }
        }

        .p-highlight .p-tabview-nav-link {
          background: transparent;
          border-bottom-color: #3b82f6;
          color: #3b82f6;
        }
      }

      .p-tabview-panels {
        background: transparent;
        padding: 1rem 0 0 0;
        border: none;
      }
    }
  }

  // AutoComplete styling
  ::ng-deep {
    .p-autocomplete {
      .p-inputtext {
        &:focus {
          border-color: #3b82f6;
          box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
        }
      }

      .p-autocomplete-panel {
        border: 1px solid #d1d5db;
        border-radius: 0.375rem;
        box-shadow:
          0 10px 15px -3px rgba(0, 0, 0, 0.1),
          0 4px 6px -2px rgba(0, 0, 0, 0.05);

        .p-autocomplete-items {
          .p-autocomplete-item {
            padding: 0;
            border: none;

            &:hover {
              background: #f3f4f6;
            }

            &.p-highlight {
              background: #eff6ff;
              color: #1e40af;
            }
          }
        }
      }
    }

    .p-multiselect {
      .p-multiselect-label {
        &:focus {
          border-color: #3b82f6;
          box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
        }
      }

      .p-multiselect-panel {
        border: 1px solid #d1d5db;
        border-radius: 0.375rem;
        box-shadow:
          0 10px 15px -3px rgba(0, 0, 0, 0.1),
          0 4px 6px -2px rgba(0, 0, 0, 0.05);

        .p-multiselect-header {
          background: #f9fafb;
          border-bottom: 1px solid #e5e7eb;
          padding: 0.75rem;

          .p-multiselect-filter-container {
            .p-inputtext {
              border: 1px solid #d1d5db;
              border-radius: 0.375rem;
              padding: 0.5rem 0.75rem;

              &:focus {
                border-color: #3b82f6;
                box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
              }
            }
          }
        }

        .p-multiselect-items {
          .p-multiselect-item {
            padding: 0.5rem 0.75rem;
            border: none;

            &:hover {
              background: #f3f4f6;
            }

            &.p-highlight {
              background: #eff6ff;
              color: #1e40af;
            }
          }
        }
      }
    }
  }

  // Selected zone tags styling
  .selected-zone-tag {
    background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);

    &:hover {
      background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
    }

    button {
      &:hover {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }

  // Clear button styling
  .clear-button {
    transition: all 0.2s ease;

    &:hover {
      background: #fef2f2;
      border-color: #fca5a5;
      color: #dc2626;
      transform: translateY(-1px);
      box-shadow:
        0 4px 6px -1px rgba(0, 0, 0, 0.1),
        0 2px 4px -1px rgba(0, 0, 0, 0.06);
    }

    &:active {
      transform: translateY(0);
    }
  }

  // Disabled state styles
  &.disabled {
    opacity: 0.6;

    .btn-icon.toggle,
    .btn-icon.clear {
      cursor: not-allowed;
      background: #f9fafb;
      color: #9ca3af;

      &:hover {
        background: #f9fafb;
        color: #9ca3af;
      }
    }

    .tag-remove {
      cursor: not-allowed;

      &:hover {
        color: #ffffff;
      }
    }

    ::ng-deep {
      .p-autocomplete .p-inputtext {
        background: #f9fafb;
        color: #9ca3af;
        cursor: not-allowed;

        &:focus {
          border-color: #d1d5db;
          box-shadow: none;
        }
      }
    }
  }
}

// Responsive adjustments
@media (max-width: 768px) {
  .zone-filter-field {
    .custom-tabview {
      ::ng-deep {
        .p-tabview-nav {
          .p-tabview-nav-link {
            padding: 0.5rem 0.75rem;
            font-size: 0.875rem;
          }
        }
      }
    }
  }
}
